define('taoQtiTest/runner/helpers/isReviewPanelEnabled',['taoQtiTest/runner/helpers/map'], function (mapHelper) { 'use strict';

    mapHelper = mapHelper && Object.prototype.hasOwnProperty.call(mapHelper, 'default') ? mapHelper['default'] : mapHelper;

    /**
     * Tells if the review panel is enabled
     * @returns {Boolean}
     */
    function isReviewPanelEnabled(runner) {
      const reviewEnabled = mapHelper.hasItemCategory(runner.getTestMap(), runner.getTestContext().itemIdentifier, 'reviewScreen', true);
      const itemReviewEnabled = runner.getOptions().review.enabled;
      return reviewEnabled && itemReviewEnabled;
    }

    return isReviewPanelEnabled;

});

